<template>
  <div id="app">
    <header>
      <div class="logo">
        <img src="./assets/logo.svg" alt="Logo">
        GLOBAL
      </div>
      <transition mode="out-in" name="hireus">
        <button class="hireus" @click="chgScreen(5)" v-if="screen != 1 && screen != 5">Hire us</button>
      </transition>
      <button class="ham-wrapper" @click="disableScroll()">
        <div class="hamburger"></div>
      </button>
    </header>
    <aside v-bind:class="{ 'scale-open-menu': openMenu}">
      <div class="scale"></div>
      <div v-bind:class="{ 'scale-active': screen == 1}" class="scale-home">
        01
        <transition mode="out-in" :name="direction"><span v-if="screen == 1">Home</span></transition>
      </div>
      <div v-bind:class="{ 'scale-active': screen == 2}" class="scale-works">
        02 
        <transition mode="out-in" :name="direction"><span v-if="screen == 2">Works</span></transition>
      </div>
      <div v-bind:class="{ 'scale-active': screen == 3}" class="scale-about">
        03
        <transition mode="out-in" :name="direction"><span v-if="screen == 3">About</span></transition>
      </div>
      <div v-bind:class="{ 'scale-active': screen == 4}" class="scale-contact">
        04
        <transition mode="out-in" :name="direction"><span v-if="screen == 4">Contact</span></transition>
      </div>
      <div v-bind:class="{ 'scale-active': screen == 5}" class="scale-hireUs">
        05
        <transition mode="out-in" :name="direction"><span v-if="screen == 5">Hire us</span></transition>
      </div>
    </aside>
    <transition mode="out-in" :name="contentDirection">
      <router-view v-bind:class="{ 'content-open-menu': openMenu}" @changeScreen="chgScreen"/>
    </transition>
    <div class="menu-wrapper" v-bind:class="{ 'wrapper-open-menu': openMenu}">
      <button v-bind:class="{ 'menu-button-active': screen == 1}" @click="chgScreen(1)">
        Home <div></div>
      </button>
      <button v-bind:class="{ 'menu-button-active': screen == 2}" @click="chgScreen(2)">
        Works <div></div>
      </button>
      <button v-bind:class="{ 'menu-button-active': screen == 3}" @click="chgScreen(3)">
        About <div></div>
      </button>
      <button v-bind:class="{ 'menu-button-active': screen == 4}" @click="chgScreen(4)">
        Contact <div></div>
      </button>
      <button v-bind:class="{ 'menu-button-active': screen == 5}" @click="chgScreen(5)">
        Hire us <div></div>
      </button>
    </div>
  </div>
</template>

<script>

export default {
  name: 'App',
  created () {
    var lastScrollTop = 0;
    var max = 2800 - window.innerHeight;

    window.addEventListener('scroll', function () {
      if(document.body.style.overflow != 'hidden'){
        var st = window.pageYOffset || document.documentElement.scrollTop;
        st = Math.floor(st/max*5) + 1;
        if (st > this.screen){
          this.chgDirection(1);
          this.scaleDown();
        } else if (st < this.screen) {
          this.chgDirection(-1);
          this.scaleUp();
        }
        lastScrollTop = st <= 0 ? 0 : st; // For Mobile or negative scrolling
      }
    }.bind(this));

    var pages = ['home', 'works', 'about', 'contact', 'hire'];
    var page = (pages.indexOf(this.$router.currentRoute.name.toLowerCase()) + 1 > 0) ?
                  pages.indexOf(this.$router.currentRoute.name.toLowerCase()) + 1 : 1;
    page = (page == 0) ? 1 : page;
    this.screen = page;
    window.scrollTo(0,max*((page - 1)*.2));
    setTimeout(function () {
        let offset;
        offset = document.querySelector('.scale-active').offsetTop + 5;
        document.querySelector('.scale').style.setProperty('--offsetTop', offset + "px");
        console.log('1');
      }, 0);
    
  },
  data() {
    return {
      screen: 1,
      direction: 'scale-down',
      openMenu: false,
      contentDirection: 'content-down',
    }
  },
  methods: {
    scaleDown() {
      if(this.screen < 5){
        this.screen += 1;
      }

      this.chgPage;
    },
    scaleUp() {
      if(this.screen > 1){
        this.screen -= 1;
      }

      this.chgPage;
    },
    chgScreen(screen) {
      if(this.screen > screen) {
        this.chgDirection(1);
      }else if (this.screen < screen) {
        this.chgDirection(-1); 
      }
      this.screen = screen;
      this.chgPage;
      this.chgScroll;
    },
    disableScroll() {
      this.openMenu = !this.openMenu;
      if(this.openMenu){
        document.body.style.overflow = 'hidden';
      }else {
        document.body.style.overflow = 'initial';
      }
      this.chgScroll;
    },
    chgDirection(dir) {
      this.direction = (dir == 1) ? 'scale-down' : 'scale-up';
      this.contentDirection = (dir == 1) ? 'content-down' : 'content-up';
    }
  },
  computed: {
    chgPage() {
      var pages = ['home', 'works', 'about', 'contact', 'hire'];
      if(this.$router.currentRoute.name.toLowerCase() != pages[this.screen - 1]) {
        this.$router.push(`/${pages[this.screen - 1]}`);    
      }
      
      setTimeout(function () {
        let offset;
        offset = document.querySelector('.scale-active').offsetTop + 5;
        document.querySelector('.scale').style.setProperty('--offsetTop', offset + "px");
      }, 0);
    },
    chgScroll() {
      var max = 2800 - window.innerHeight;
      window.scrollTo(0,max*((this.screen - 1)*.2));
      setTimeout(function () {
        let offset;
        offset = document.querySelector('.scale-active').offsetTop + 5;
        document.querySelector('.scale').style.setProperty('--offsetTop', offset + "px");
        console.log('1');
      }, 0);
    }
  }
}


  
</script>

<style lang="scss">
.fake {
  margin-top: 2800px;
}
body {
  margin: 0;
  background: #040404;
  max-height: 100vh;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
}

header {
  padding: 1.313rem 3.5rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  
}

button {
  cursor: pointer;
}

.menu-wrapper {
  position: absolute;
  top: 50%;
  transform: translate(200%,-50%);
  right: 10rem;
  transition: transform 1s linear;
  text-align: right;
  display: flex;
  max-width: 280px;
  flex-wrap: wrap;
  justify-content: flex-end;

  button {
    border: none;
    background: none;
    outline: none;
    font-size: 3.438rem;
    font-weight: 900;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.31;
    letter-spacing: 1.38px;
    color: #ffffff;
    display: block;
    position: relative;
    overflow: hidden;
    padding: 0.125rem 0.875rem;

    div {
      width: 106%;
      position: absolute;
      top: 50%;
      transform: translate(100%,0);
      height: 15px;
      background: #0f33ff;
      transition: transform .4s linear;
    }
  }
}

.wrapper-open-menu {
  transform: translate(0,-50%);
  display: flex;
}

.menu-wrapper .menu-button-active div {
    transform: translate(-10%,0);
}

.ham-wrapper {
  width: 30px;
  height: 20px;
  border: none;
  background: none;
  outline: none;
  z-index: 10;
}

.logo {
  font-weight: 900;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: 0.8px;
  text-align: left;
  color: #ffffff;
  display: flex;
  align-items: center;

  img {
    margin-right: .5rem;
  }
}

.hireus {
  border: none;
  width: 90px;
  height: 28px;
  background-color: #0f33ff;
  font-size: 0.875rem;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: 0.7px;
  color: #fff;
  outline: none;
  cursor: pointer;
  transform: translateX(-50%);
  z-index: 10;

  &:hover {
    background: #737373;
    transition: background .2s linear;
  }
}
.hamburger {
  width: 15px;
  height: 2px;
  background-color: #fff;
  position: relative;

  &::before, &::after {
    content: '';
    width: 19px;
    height: 2px;
    position: absolute;
    background: #fff;
    top: -5px;
    left: -2px;
  }

  &::after {
    top: 5px;
  }
}

aside {
  $height: 100%;
  margin: 2.375rem 1.875rem 3.875rem;
  width: 100px;
  height: calc(#{$height} - 49px - 6.25rem);
  position: absolute;
  font-size: 0.875rem;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  color: rgba(255, 255, 255, 0.3);
  transition: transform .5s 1s linear;

  div {
    margin-bottom: 5.34rem;
    transition: color .4s;
  }

  div:nth-child(2) {
    margin-top: 1.5rem;
  }
}

.scale {
  width: 2px;
  height: 100%;
  margin-left: 1.5rem;
  background-color: #ffffff26;
  position: absolute;


  &-active {
    color: #fff;
  }

  &::after {
    content: '';
    position: absolute;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    top: 0;
    background: #fff;
    left: -2px;
    transform: translateY(var(--offsetTop,30px));
    transition: transform .37s linear;
  }

}

.scale-open-menu {
  transform: translate(-120%,0);
  transition: transform .5s ease-in-out;
}

.slider-wrapper .slide {
  border-radius: 50%;
}

.workImg {
  border-radius: 50%;
}

.map {
  max-width: 1280px;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-55%,-20%);
  user-select: none;
}

.arrow {
    width: 20px;
    height: 2px;
    background: #fff;
    position: relative;

    &::before, &::after {
      content: '';
      position: absolute;
      width: 10px;
      height: 2px;
      right: 0;
      background: inherit;
      transform: rotate(45deg) translate(-1px,-3px);
    }

    &::after {
      transform: rotate(-45deg) translate(-1px,3px);
    }
  }

.content {
  transform: perspective(300px);
  transition: transform 1s linear, box-shadow .4s .6s;
  margin-left: 10rem;
}

.content-open-menu {
  transform: perspective(500px) rotateX(0deg) rotateY(23deg) rotateZ(0deg) scale(.45) translate(-35%,-10%);
  transform-origin: center center;
  box-shadow: 0px 0px 25px 10px rgba(0, 0, 0, 0.75), 0px 0px 0px 25px #0f33ff;
  padding: 2.5rem;
  margin-right: 0;
  width: 100%;
  max-width: 1109px;
  max-height: 580px;
}

aside span {
    transform: translate(1rem,0);
    display: inline-block;
  }
.scale-down-enter-active, .scale-down-leave-active, .scale-up-enter-active, .scale-up-leave-active {
  transition: transform .4s linear, opacity .2s linear;
}
.scale-down-enter, .scale-down-leave-to {
  transform: translate(1rem,-5.34rem);
  opacity: 0;
}
.scale-down-leave-to {
  transform: translate(1rem,9rem);
}

.scale-up-enter, .scale-up-leave-to {
  transform: translate(1rem,5.34rem);
  opacity: 0;
}
.scale-up-leave-to {
  transform: translate(1rem,-9rem);
}

.content-up-enter-active, .content-up-leave-active, .content-down-enter-active, .content-down-leave-active{
  transition: transform .4s linear, opacity .2s linear;
}
.content-up-enter, .content-up-leave-to {
  transform: translate(0,100%);
  opacity: 0;
}
.content-up-leave-to {
  transform: translate(0,-100%);
}

.content-down-enter, .content-down-leave-to {
  transform: translate(0,-100%);
  opacity: 0;
}
.content-down-leave-to {
  transform: translate(0,100%);
}

.hireus-enter-active, .hireus-leave-active{
  transition: transform .4s linear, opacity .2s linear;
}
.hireus-enter, .hireus-leave-to {
  transform: translate(-50%,100%);
  opacity: 0;
}
.hireus-leave-to {
  transform: translate(-50%,-100%);
}

</style>



