<template>
  <div class="about content">
    <div class="wrapper">
      <h1>We <br> believe in <br> passionate <br> people <span>.</span></h1>
      <button>Career <div class="arrow"></div></button>
      <div class="container">
        <div>
          <img src="../assets/layer-4.jpg" alt="">
          <p>WINNERS</p>
        </div>
        <div>
          <img src="../assets/layer-5.jpg" alt="">
          <p>PHILOSOPHY</p>
        </div>
        <div>
          <img src="../assets/layer-6.jpg" alt="">
          <p>HISTORY</p>
        </div>
      </div>
    </div>
    <img src="../assets/about-img.png" alt="" class="aboutImg">
  </div>
</template>

<script>
export default {
  name: 'About',
  data () {
    return {
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
h1 {
  font-size: 5.125rem;
  font-weight: 900;
  font-stretch: normal;
  font-style: normal;
  line-height: 0.88;
  letter-spacing: 2.05px;
  text-align: left;
  color: #ffffff;
  margin-bottom: 3rem;

  span {
    color: #0f33ff;
  }
}

button {
  width: 100px;
  height: 11px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: none;
  border: none;
  outline: none;
  font-size: 0.875rem;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 5.14;
  letter-spacing: 0.7px;
  text-align: left;
  color: #ffffff;

  &:hover {
    color: #0f33ff;
    transition: color .2s linear;

    div {
      background: #0f33ff;
      transition: background .2s linear;
    }
  }
}

.aboutImg {
  position: absolute;
  top: -3rem;
  right: 15rem;
}

.container {
  display: flex;
  max-width: 569px;
  justify-content: space-between;
  margin-top: 2.5rem;

  div {
    max-width: 169px;
  }

  img {
    border: solid 11px #0f33ff;
    box-sizing: border-box;
    width: 100%;
    object-fit: none;
  }

  p {
    font-size: 0.875rem;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: 0.7px;
    text-align: left;
    color: #ffffff;
    margin: -.75rem 0 0 1.25rem;
  }
}
</style>



