<template>
  <div class="works content">
    <h1>Selected work</h1>
    <div class="slider">
      <button @click="slide(-1)" class="prev"><div class="arrow"></div></button>
      <div class="slider-wrapper">
        <div class="slide" :class="slides[0]">
          <img class="workImg" src="../assets/layer-3.jpg" alt="">
          <h5>Victory</h5>
          <h6>Lorem ipsum dolor sit amet, <br> consectetur adipisicing elit, sed do</h6>
        </div>
        <div class="slide" :class="slides[1]">
          <img class="workImg" src="../assets/layer-1.jpg" alt="">
          <h5>Metiew & Smith</h5>
          <h6>Lorem sit amet ipsum dolor, <br> consectetur adipisicing elit, sed do</h6>
        </div>
        <div class="slide" :class="slides[2]">
          <img class="workImg" src="../assets/layer-2.jpg" alt="">
          <h5>Alex nowak</h5>
          <h6>Lorem dolor ipsum sit amet, <br> consectetur adipisicing elit, sed do</h6>
        </div>
      </div>
      <button @click="slide(1)" class="next"><div class="arrow"></div></button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Works',
  data () {
    return {
      slides: ['slideLeft','slideCentre','slideRight'],
    }
  },
  methods: {
    slide(dir) {
      if(dir == -1) {
        var first = this.slides.shift();
        this.slides.push(first);
      }else {
        var last = this.slides.pop();
        this.slides.unshift(last);
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.works {
  margin-right: 10rem; 
  height: 100%;
}
h1 {
  font-size: 1.875rem;
  font-weight: 900;
  font-stretch: normal;
  font-style: normal;
  line-height: 2.4;
  letter-spacing: 0.75px;
  text-align: center;
  color: #ffffff;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

button {
  border: none;
  border-radius: 50%;
  width: 54px;
  height: 54px;
  background-color: #282828;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: none;
}

.slider-wrapper {
  position: relative;
  transform: translate(-50%,-50%);
}

.slide {
  height: 167px;
  width: 167px;
  background-color: #fff;
  position: absolute;
  left: 0;
  top: 0;

  img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }

  h5 {
    font-size: 0.75rem;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: 0.6px;
    text-align: center;
    color: rgba(255, 255, 255, 0.5);
    margin-top: 0.75rem;
    margin-bottom: 1.5rem;
  }

  h6 {
    display: none;
  }
}
.slideLeft {
  transform: translate(-250%,-50%);
}

.slideCentre {
  transform: scale(1.5) translate(-37.5%,-37.5%);

  h5 {
    font-size: 1.125rem;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: 0.9px;
    color: #ffffff;
  }

  h6 {
    display: block;
    width: 100%;
    font-size: .625rem;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: normal;
    text-align: center;
    color: #ffffff;
    margin: 0;
    transform: translate(0,0);
  }
}

.slideRight {
  transform: translate(150%,-50%);
}

.prev {
  transform: rotate(180deg);
}

.next {
  order: 5;
}

.slider {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 10rem;
}

</style>



