<template>
  <div class="contact content">
    <img src="../assets/map.png" alt="" class="map">
    <div class="wrapper">
      <h4 class="address">
        Pawia 5,31-154 Kraków, Poland <br>
        ouremail@gmail.com <br>
        +48 12 628 75 60
      </h4>
      <button>Behance</button>
      <button class="Dribbble">Dribbble</button>
      <button class="contactUs">CONTACT US</button>
    </div>
  </div>
