<script>
export default {
  name: 'Contact',
  data () {
    return {
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

.contact {
  height: 100%;
}

.wrapper {
  max-width: 302px;
  height: 361px;
  background: #000;
  transform: translateX(200%);
  margin-top: 5rem;
}

h4 {
  font-size: 0.875rem;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 2.57;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
  margin-top: 2.5rem;
  margin-bottom: 0;
}

button {
  width: 132px;
  height: 41px;
  background-color: #1769ff;
  border: none;
  display: block;
  margin: 1.625rem auto;
  outline: none;
  font-size: 0.875rem;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: 0.7px;
  color: #ffffff;
}

.Dribbble {
  background-color: #ea4c89;
}

.contactUs {
  background-color: #0f33ff;
}

</style>



