import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import About from '@/components/About'
import Works from '@/components/Works'
import Contact from '@/components/Contact'
import Hireus from '@/components/Hireus'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/fifthSite/',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/works',
      name: 'Works',
      component: Works
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact
    },
    {
      path: '/hire',
      name: 'Hire',
      component: Hireus
    }
  ]
})



