<template>
  <div class="hire content">
    <h1>You want us to do</h1>
    <div class="wrapper">
      <button v-bind:class="{ 'button-active': active == 1}" 
              @click="active = (active != 1) ? 1 : 0">
              <div class="check"></div>App Design
      </button>
      <button v-bind:class="{ 'button-active': active == 2}" 
              @click="active = (active != 2) ? 2 : 0">
              <div class="check"></div>Graphic Design
      </button>
      <button v-bind:class="{ 'button-active': active == 3}" 
              @click="active = (active != 3) ? 3 : 0">
              <div class="check"></div>Motion Design
      </button>
      <button v-bind:class="{ 'button-active': active == 4}" 
              @click="active = (active != 4) ? 4 : 0">
              <div class="check"></div>UX Design
      </button>
      <button v-bind:class="{ 'button-active': active == 5}" 
              @click="active = (active != 5) ? 5 : 0">
              <div class="check"></div>Web Design
      </button>
      <button v-bind:class="{ 'button-active': active == 6}" 
              @click="active = (active != 6) ? 6 : 0">
              <div class="check"></div>Marketing
      </button>
    </div>
    <form @submit="cancelEvent">
      <input type="text" placeholder="Name">
      <input type="text" placeholder="Email">
      <button>SEND REQUEST</button>
    </form>
  </div>
</template>

<script>
export default {
  name: 'Hire',
  data () {
    return {
      active: 0,
    }
  },
  methods: {
    cancelEvent(event) {
      event.preventDefault();
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">

.hire {
  margin-right: 10rem;
}

h1 {
  font-size: 1.875rem;
  font-weight: 900;
  font-stretch: normal;
  font-style: normal;
  line-height: 2.4;
  letter-spacing: 0.75px;
  text-align: center;
  color: #ffffff;
}

.button-active {
  border: none;
  background-color: #0f33ff;
  .check {
    display: block;
  }
}

button {
  outline: none;
  background: none;
  width: 200px;
  padding: 0;
  height: 52px;
  border: solid 2px #ffffff;
  font-size: 1.125rem;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: 0.9px;
  color: #ffffff;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1.5rem;
  cursor: pointer;

  .check {
    height: 2px;
    width: 14px;
    background: #fff;
    transform: rotate(-45deg);
    position: relative;
    display: none;
    margin-right: 8px;

    &::after {
      height: 2px;
      width: 8px;
      background: #fff;
      content: '';
      position: absolute;
      top: 0;
      transform: rotate(90deg) translate(-3px, 11px);
    }
  }
}

.wrapper {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-content: flex-start;
  max-width: 999px;
}

form {
  margin: 1rem auto;
  max-width: 771px;

  button {
    width: 401px;
    height: 52px;
    background-color: #0f33ff;
    border: none;
    margin: 3.875rem auto;
  }
}

input {
  max-width: 315px;
  border: none;
  background: none;
  font-size: 1.875rem;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: 2.4;
  letter-spacing: 1.5px;
  text-align: left;
  color: #ffffff7f;
  border-bottom: solid 2px #ffffff7f;
  margin: 0 2rem;
  outline: none;
}

</style>



